<template>
        <el-form :inline="true">
          <el-form-item label="请选择库位：">
            <el-cascader
              :options="shelveTreeData"
              v-model="shelves"
              @change="shelvesChange"
              :props="defaultProps">
            </el-cascader>
          </el-form-item>
        </el-form>
</template>

<script>
/**
 * User Search Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.09.25
 */
import { setSession, getSession } from '@/utils/session'

export default {
  name: 'Cascader',
  data () {
    return {
      shelves:[]
    }
  },
  props: {
    defaultShelves:{
      type:Array
    },
    shelveTreeData:{
      type:Array
    },
    defaultProps:{
      type:Object
    }
  },
  mounted () {
    this.shelves = this.defaultShelves
  },
  methods: {
    shelvesChange(res){
      this.$emit("change",res)
    }
  }

}
</script>
<style lang="scss" scoped>
.el-cascader{
  width: 300px;
}
</style>



